@import 'variables';
@import 'mixins';

@include for-mobile-only {
    .footer {
        position: fixed;
        right: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        
        img {
            width: 25px;
            margin: 0 8px 8px 0;
            filter: invert(0.5);
            transition: 0.3s;
            &:hover {
                transform: scale(1.2);
                filter: invert(0.7);
            }
        }
    }
}

@include for-desktop-up {
    .footer {
    position: fixed;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    
    img {
        width: 40px;
        margin: 0 8px 8px 0;
        filter: invert(0.5);
        transition: 0.3s;
        &:hover {
            transform: scale(1.2);
            filter: invert(0.7);
        }
    }
    }
}

